@import '../styles/base';

.login {   
width: 40%;
height: 70vh;
top: 50%;
left: 50%;
transform: translate(-50%,-50%);
background: rgb(0,0,0);
background: radial-gradient(circle, rgba(0,0,0,1) 0%, rgba(18,115,112,1) 100%);
position: absolute;
display: flex;
align-items: center;
justify-content: center;
z-index: 3;
border-radius: 2rem;

&__form{
    h1{
        color: $color-gray-brigh;
        text-align:center;
        font-size: 3rem;
        padding: 2rem 1rem;
        margin-bottom: 2rem;
    }
    
    
      label{
        text-align: left;
        margin-bottom: .5rem;
        color:white;
        font-size: 1.5rem;
        font-weight:400;
        text-transform:capitalize;
      }
}
&__formbox{
  font-size: 1.6rem;
 color:#666;
    display: flex;
    flex-direction: column;
   justify-content: center;
    margin-bottom: 1rem;
    height: 7.5rem;
  
    span{
      height: 1rem;
      text-align: left;
      margin-top: .5rem;
      width: fit-content;
      font-size: 1.3rem;
      font-weight: 400;
      color: red;
      line-height:1rem;
      padding: .2rem .3rem;
      border-radius: 3px;
      
    }
  }
  
&__checkbox{
    height: 8vh;
    display: flex;
    font-size: 0.85rem;
    flex-direction: column ;
    align-items: center;
    text-align: center;
  
  label{
  font-size: 1.5rem;
  
  }
    span{
      height: 1rem;
      text-align: left;
      margin-top: .5rem;
      width: fit-content;
      font-size: 1.2rem;
      font-weight: 400;
      color: red;
      line-height: 1rem;
      padding: 2px 3px;
      border-radius: 3px;
    }
    input[type="checkbox"] {
      margin-left: 2rem;
      width: 1.8rem;
      height: 1.8rem;
      border: 1px solid #1a73e8;
      appearance: none;
      border-radius: 5px;
      transition: all 0.2s ease-in;
    }
    
    input[type="checkbox"]:checked {
      width: 1.88rem;
      height: 1.8rem;
      background-color:$color-root;
      border-radius: 50%;
    }
    
    input[type="checkbox"]:focus {
      height: 1.8rem;
      width: 1.8rem;
      outline: none;
      border: 2px solid #004aaa;
    }
  }
  
  &__formInput {
    width: 30rem;
    height: 3rem;
    border-radius: .5rem;
    padding: 0rem 1rem;
    border: .1rem solid silver;
    transition: all 0.2s ease-in;
  
  }
  
  &__uncompleted {
    width: 30rem;
    height: 3rem;
    border-radius: .5rem;
    padding: 0px 1rem;
    border: .1rem solid red;
    transition: all 0.2s ease-in;
  }
  
  &__formInput:focus,
  &__uncompleted:focus {
    outline: none;
    line-height: 3rem;
    height: 3rem;
    border: .1rem solid $color-root;
  }
  
  &__signbutton{
    margin-top: 5rem;
    display: flex;
    justify-content: center;
    align-items: center;
    
    button{
      width: 60%;
      height: 5vh;
      background-color: $color-main;
      border: 1px solid $color-root;
      color:$color-root;
      border-radius:.8rem;
      font-size: 2rem;
      font-weight: 400;
      cursor: pointer;
      transition: all 0.6s;
      &:hover{
      box-shadow: 0 .2rem .3rem 0 $color-root;
    
      }
    }
  
  }
  &__login{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 2rem;
    a{
      font-size: 1.5rem;
      font-weight: 400;
      text-decoration: none;
      color: aliceblue;
      transition: all .6s;
      &:hover{
        font-weight: 500;
        color: white;
      }
    }
  }
  }
